<template>
  <div>
    <div class="tool-bar">
      <el-button type="primary" size="mini" @click="insertSymbol"
        ></el-button
      >
      <el-button type="primary" size="mini" @click="insertParam"
        ></el-button
      >
    </div>
    <!--  -->
    <quill-editor
      class="editor"
      v-model="content"
      ref="quillEditor"
      :options="editorOption"
      @change="onEditorChange($event)"
    ></quill-editor>
  </div>
</template>

<script>
// 
const toolbarOptions = [
  ["bold", "italic", "underline", "strike"], //    
  ["blockquote", "code-block"], //   
  [{ list: "ordered" }, { list: "bullet" }], // 
  [{ indent: "-1" }, { indent: "+1" }], // 
  [{ size: ["small", false, "large", "huge"] }], // 
  [{ header: [1, 2, 3, 4, 5, 6, false] }], // 
  [{ color: [] }, { background: [] }], // 
  [{ align: [] }], // 
  ["clean"], // 
  // ["link", "image", "video"], // 
];

import "quill/dist/quill.core.css";
import "quill/dist/quill.snow.css";
import "quill/dist/quill.bubble.css";
import { quillEditor } from "vue-quill-editor";
import Quill from "quill";
import ImageResize from "quill-image-resize-module";
Quill.register("modules/imageResize", ImageResize);

// var BlockEmbed = Quill.import('blots/block/embed')
var BlockEmbed = Quill.import("blots/embed");
class ImageBlot extends BlockEmbed {
  static create(value) {
    const node = super.create();
    node.setAttribute("src", value.url);
    node.setAttribute("controls", value.controls);
    node.setAttribute("width", value.width);
    node.setAttribute("height", value.height);
    return node;
  }
  static value(node) {
    return {
      url: node.getAttribute("src"),
      controls: node.getAttribute("controls"),
      width: node.getAttribute("width"),
      height: node.getAttribute("height"),
    };
  }
}
ImageBlot.blotName = "image";
ImageBlot.tagName = "img";
Quill.register(ImageBlot);
export default {
  components: { quillEditor },
  props: {
    /*  */
    contentValue: {
      type: String,
      default: "",
    },
  },
  data() {
    return {
      content: this.contentValue,
      editorOption: {
        theme: "snow", // or 'bubble'
        placeholder: "",
        modules: {
          toolbar: {
            container: toolbarOptions,
            handlers: {
              image: function (value) {
                if (value) {
                  // input
                  document.querySelector(".quill-img input").click();
                } else {
                  this.quill.format("image", false);
                }
              },
            },
          },
        },
      },
    };
  },
  watch: {
    contentValue: {
      // 
      handler(val) {
        this.content = val;
      },
      immediate: true,
      deep: true,
    },
  },
  mounted() {},
  methods: {
    insertSymbol() {
      const quill = this.$refs.quillEditor.quill;
      const length = quill.selection.savedRange.index;
      console.log("", quill, length);
      const url = window.location.origin + "/card" + "/img/fh1.jpeg";
      quill.insertEmbed(length, "image", {
        url: url,
        width: "auto",
        height: 25 + "px",
      });
      quill.setSelection(length + 1);
    },
    insertParam() {
      const quill = this.$refs.quillEditor.quill;
      // const length = quill.selection.savedRange.index;
      console.log("");
      // quill 
      var num = quill.selection.savedRange.index; //
      //  num data   { color: 'rgb(230, 0, 0)' } 
      const data = "[2020]";
      quill.insertText(num, data);
      // quill.insertText(num + data.length, " ", {
      //   color: "rgba(0, 0, 0, 0.65)",
      // });
      quill.selection.savedRange.index = num + data.length + 1; //(+1 )
    },
    onEditorChange() {
      // 
      console.log("", this.content);
      this.$emit("input", this.content);
    },
    getContent() {
      const result = this.content
        .replace(/<p>/g, "")
        .replace(new RegExp("</p>", "gm"), "");

      console.log("result", result);
      return result;
    },
  },
};
</script>
<style lang='scss'>
.tool-bar {
  width: 100%;
  display: flex;
  padding-bottom: 5px;
}
.ql-editor {
  height: 200px;
}
</style>
